import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl} from '@angular/forms';
import {DatatableService} from '../datatable.service';
import {filter} from 'rxjs/operators';
import {combineLatest, Subscription} from 'rxjs';
import {Paginator} from '../../shared/paginator.service';
import {Model} from '../../core/types/models/model';

@Component({
    selector: 'datatable-footer',
    templateUrl: './datatable-footer.component.html',
    styleUrls: ['./datatable-footer.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DatatableFooterComponent implements OnInit, OnDestroy {
    public perPageControl = new FormControl(15);
    public from: number;
    public to: number;
    public totalRecords: number;
    public havePrevious: boolean;
    public haveNext: boolean;
    private changeRef: Subscription;

    constructor(
        private datable: DatatableService<any>,
        private cd: ChangeDetectorRef,
        private paginator: Paginator<Model>,
    ) {}

    ngOnInit() {
        this.changeRef = combineLatest([this.datable.pagination$, this.datable.total$])
            .subscribe(([pagination, total]) => {
                this.to = pagination.perPage * pagination.page;
                this.from = (this.to - pagination.perPage) || 1;
                this.totalRecords = total.records;
                this.havePrevious = pagination.page > 1;
                this.haveNext = pagination.page < total.pages;
                this.cd.markForCheck();
            });

        this.perPageControl.valueChanges
            .pipe(filter(value => value !== this.datable.pagination$.value.perPage))
            .subscribe(perPage => {
                this.datable.pagination$.next({
                  ...this.datable.pagination$.value,
                  perPage
                });
            });
    }

    ngOnDestroy() {
        this.changeRef.unsubscribe();
        this.paginator.destroy();
    }

    public previousPage() {
        this.datable.pagination$.next({
            ...this.datable.pagination$.value,
            page: (this.datable.pagination$.value.page - 1) || 1
        });
    }

    public nextPage() {
        const current = this.datable.pagination$.value.page;
        const total = this.datable.total$.value.pages;
        const next = current + 1;
        this.datable.pagination$.next({
            ...this.datable.pagination$.value,
            page: !current || next > total ? current : next
        });
    }
}
